declare var require: any
import 'hammerjs';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { AppComponent } from './app.component';

import { Md2Module }  from 'md2';

// components
// import { QuillEditorModule } from './components/ngx-quill-editor';
import { QuillEditorModule } from 'ngx-quill-editor';

// projects
import { QuillEditorComponent } from './projects/ngx-quill-editor';
import { QuillEditorComponentExample01 } from './components/ngx-quill-editor/examples/01-example.component';
import { QuillEditorComponentExample02 } from './components/ngx-quill-editor/examples/02-example.component';
import { QuillEditorComponentExample03 } from './components/ngx-quill-editor/examples/03-example.component';

@NgModule({
  declarations: [
    AppComponent,
    QuillEditorComponent,
    QuillEditorComponentExample01,
    QuillEditorComponentExample02,
    QuillEditorComponentExample03
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    HttpModule,
    Md2Module.forRoot(),
    QuillEditorModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



